@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap);
* {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}
.container {
  margin-left: 75px;
  margin-right: 75px;
}
.MuiToolbar-root {
  background-color: #3a86ff;
  
}
.MuiButton-root{
  color: white !important;
}


@media screen and (max-width: 500px) {
  .container{
    margin-left: 30px;
    margin-right: 30px;  
  }
}


.front-flex {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #fcbf49;
}
.front-para {
  text-align: center;
}
.foot-color-flex {
  display: flex;
  background-color: #e0e0e0;
}
.front-img{
  height: 400px;
  width: 400px;
}
.front-button {
  border: none;
  outline: none;
  background: #7bdff2;
  padding: 8px 25px;
  color: #eee;
  font-weight: 600;
  letter-spacing: 2px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.front-button:hover{
  border: none;
  outline: none;
  background: #3a86ff;
  padding: 8px 25px;
  color: #fff;
  font-weight: 600;
  letter-spacing: 2px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.front-para p {
  color: #ffe;
  font-weight: 600;
  font-size: 40px;
}
@media screen and (max-width: 515px) {
  .front-img{
    height: 300px;
    width: 300px;
  }
}
@media screen and (max-width: 440px) {
  .front-img{
    height: 300px;
    width: 300px;
  }
}

.between {
  background-color: #3a86ff;
}
.bet-h2 {
  color: white;
  font-weight: 700;
  letter-spacing: 2px;
  font-size: 25px;
  text-align: left;
  margin-bottom: 5px;
}
.bet-p {
  color: white;
  font-size: 20px;
  font-weight: 400;
}
.bet-flex {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.bet-worker img{
  height: 200px;
  border-radius : 50%;
}
.bet-worker{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.description-worker{
  color: white;
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center;
  padding-bottom: 20px;
}
.description-worker h6{
  font-weight: 600;
  font-size: 18px;
}
.description-worker p{
  font-weight: 500;
  font-size: 15px
}
.bet-para{
padding-top: 20px;
margin-bottom: 20px;
}
.ser-flex-img {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.ser-flex-img img {
  margin-top: 20px;
}
.ser-set{
  padding-top: 20px;
  background-color: #fcbf49;
}
.ser-flex{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center
}
.ser-head h2{
  color: white;
  font-weight: 700;
  font-size: 26px;
  margin-bottom: 10px
}
.ser-list li{
  list-style: none;
  color: white;
  margin-bottom: 10px;
  font-weight: 500;

}

@media screen and (max-width: 500px) {
  .ser-img{
    height: 300px;
    width: 300px
  }
}
@media screen and (max-width: 360px) {
  .ser-img{
    height: 250px;
    width: 250px
  }
}
.req-flex {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.req-head{
  text-align: center;  
  padding-top: 20px;
  margin-bottom: 20px;
}
.req-head h2{
  font-weight: 700;
  font-size: 25px;
  color: white;
}
.main-req{
  background-color: #3a86ff;
}
.req-para p {
  color: white;
  font-size: 18px;
  font-weight: 500;
  
}
.req-para{
  text-align: center;
}
@media screen and (max-width: 420px) {
  .req-img{
    height: 300px;
    width: 300px
  }
}
.foot-color-flex{
  font-size: 20px;
  font-weight: 700;
  text-align: left !important;
  padding: 10px;
  color: #eee;
  background-color: #3a86ff !important;
}
.foot-color-flex h6{
margin-right: 20px
}
.credit{
  font-family: Arial, Helvetica, sans-serif  !important;
  -webkit-text-decoration: double;
          text-decoration: double;
  text-transform: capitalize;
  text-shadow: 20 10 20 10 #333;
  font-weight: bolder;
  font-style: normal;
  color: rgb(255, 255, 255);
  font-size: 21px;
}
